
import React from 'react';
import { withRouter } from 'next/router';
import { connect } from 'react-redux';
import { Dropdown, Menu, Segment, Grid, Container, Input, Icon, Button } from 'semantic-ui-react';
import Axios from 'axios';
import { FaExchangeAlt } from "react-icons/fa";

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            totalData: [],
            options: []
        }
    }

    componentDidMount() {
        console.log("header");
        const { totalData } = this.state;
        const options = [];
        Axios.get('https://restcountries.eu/rest/v2/all').then(({ data }) => {
            this.setState({
                totalData: data
            });
            data.forEach((country, i) => {
                options.push(
                    {
                        key: i, text: country.name + " " + "(" + country.currencies[0].code + ")", value: country.currencies[0].code
                    }
                )
            })
            this.setState({
                options
            })

        })
    }

    firstDD = (event) => {
        console.log("firstDD");
    }

    secondDD = (event) => {
        console.log("secondDD");
    }

    render() {
        console.log("home")
        return (
            <Container>
                <Segment.Group>
                <Segment clearing inverted color="violet">
                    <Grid>
                        <Grid.Row>
                            <Grid.Column textAlign="center" width={7}>
                                <Dropdown placeholder="select" options={this.state.options} selection clearable search onChange={this.firstDD} />
                            </Grid.Column>
                            <Grid.Column textAlign="center" verticalAlign="middle" width={2}>
                                <FaExchangeAlt />
                            </Grid.Column>
                            <Grid.Column textAlign="center" width={7}>
                                <Dropdown placeholder="select" options={this.state.options} selection clearable search onChange={this.secondDD} />
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Segment>
                <Segment>
                <Grid>
                        <Grid.Row>
                            <Grid.Column textAlign="center" width={7}>
                                <Input />
                            </Grid.Column>
                            <Grid.Column textAlign="center" verticalAlign="middle" width={2}>
                                <Button primary>Convert</Button>
                            </Grid.Column>
                            <Grid.Column textAlign="center" width={7}>
                                <Input />
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Segment>
                </Segment.Group>
            </Container>
        );
    }
}
const options = [
    { key: 1, text: 'Feet', value: 1 },
    { key: 2, text: 'Meter', value: 2 },
    { key: 3, text: 'Inch', value: 3 },
]
const mapDispatchToProps = {};
export default withRouter(Home);
